import * as React from "react"
import Slider from "react-slick"
import { Button, Responsive } from "semantic-ui-react"
import * as _ from "lodash"

class Carousel extends React.Component<Props, State> {
  constructor(props) {
    super(props)
    this.onResize = _.throttle(this.onResize, 100)
    this.state = {
      screenSize: Carousel.getSize(window.innerWidth),
    }
  }

  componentDidMount() {
    window.addEventListener("resize", this.onResize)
  }

  componentWillUnmount() {
    this.onResize.cancel()
    window.removeEventListener("resize", this.onResize)
  }

  static getSize(width: number): "mobile" | "tablet" | "computer" {
    if (width < 767) {
      return "mobile"
    } else if (width < 991) {
      return "tablet"
    } else {
      return "computer"
    }
  }

  onResize: any = () => {
    const newSize = Carousel.getSize(window.innerWidth)
    const { screenSize } = this.state

    if (newSize !== screenSize) {
      this.setState({
        screenSize: newSize,
      })
    }
  }

  render(): React.ReactNode {
    const { items } = this.props
    const { screenSize } = this.state

    let slideCount, showArrow

    if (screenSize === "mobile") {
      slideCount = 1
      showArrow = false
    } else if (screenSize === "tablet") {
      slideCount = 2
      showArrow = false
    } else {
      slideCount = 3
      showArrow = true
    }

    return (
      <Slider
        slidesToShow={slideCount}
        arrows={showArrow}
        nextArrow={<div><Button icon={"angle right"} circular/></div>}
        prevArrow={<div><Button icon={"angle left"} circular/></div>}
        dots={true}
        infinite={false}>
        {
          items.map(item =>
            (
              <div className={"slide"}>
                {item}
              </div>
            )
          )
        }
      </Slider>
    )
  }
}

interface Props {
  items: Array<React.ReactNode>
}

interface State {
  screenSize: "mobile" | "tablet" | "computer"
}

export default Carousel